<template>
    <div class="shop">
        <van-nav-bar title="积分商城" left-arrow @click-left="handleToHome" :fixed="true">
            <!--<van-icon name="wap-nav" slot="right" />-->
        </van-nav-bar>
        <div class="list">
            <h5 class="list-title">热门兑换</h5>
            <van-pull-refresh v-model="isLoading" @refresh="handleOnRefresh">
                <van-list id="integral-detail-shop" :loading="loading" :finished="finished" @load="handleOnLoad">
                    <ul class="box">
                        <li class="items-row " v-for="(item,index) of newList" :key="index">
                            <div class="items-cell" v-for="(items,indexs) of item" :key="items.ID" @click="handleToDetails(items.ID)" :class="[{'left-brdr':indexs===0},{'brdr':indexs===1},{'right-brdr':indexs===2}]">
                                <div class="img-box">
                                    <img class="items-img" :src="imgSrc[index][indexs]" />
                                    <img v-if="items.IsHotSale===1" class="items-rex" src="../../assets/img/rex.png" />
                                </div>
                                    <div class="items-title items-lihei">{{items.ProdName}}</div>
                                    <div class="items-int items-lihei">
                                        <span class="int-val">{{items.DiscountIntegral}}</span>
                                        积分
                                    </div>
                                </div>
                        </li>
                        <div class="no-list" v-if="!newList.length">
                            暂无商品
                        </div>
                    </ul>
                </van-list>
            </van-pull-refresh>
        </div>
        <router-view></router-view>
    </div>
</template>

<script type="text/ecmascript-6">
import Header from "../../common/header/header";
import { mapMutations, mapGetters } from "vuex";
import { imgPath } from "../../common/dayuan";
import Back from "../../components/Back.js";

export default {
    name: "Shop",
    mixins: [Back],
    data() {
        return {
            list: [],
            isLoading: false,
            loading: false,
            finished: false,
            page: 1
        };
    },
    components: {
        Header
    },
    mounted() {},
    computed: {
        ...mapGetters(["COMP_ID"]),
        newList() {
            let List = [];
            for (let i = 0; i < this.list.length; i++) {
                let pages = Math.floor(i / 3);
                if (!List[pages]) {
                    List[pages] = [];
                }
                List[pages].push(this.list[i]);
            }
            return List;
        },
        imgSrc() {
            let imgsrc = [];
            let newArr = [];
            this.list.forEach(element => {
                for (let item in element) {
                    if (item === "ProdPicList") {
                        element[item].forEach(val => {
                            if (val.FileType === 1) {
                                imgsrc.push(imgPath() + val.FilePath);
                            }
                        });
                    }
                }
            });
            imgsrc.forEach((val, index) => {
                let pages = Math.floor(index / 3);
                if (!newArr[pages]) {
                    newArr[pages] = [];
                }
                newArr[pages].push(val);
            });
            return newArr;
        },
        listParams() {
            return {
                params: {
                    compId: this.COMP_ID,
                    state: 1,
                    // overdue: 2,  过滤已过期商品
                    page: this.page,
                    pageSize: 15
                }
            };
        }
    },
    methods: {
        ...mapMutations({ handleChangeDetails: "CHANGE_DETAILS" }),

        getList() {
            this.$toast.loading({
                mask: true,
                message: "加载中..."
            });
            this.Axios.get("/api/v1/Reg/ExchangeProdListGet", this.listParams)
                .then(res => {
                    const data = res.data.Data;
                    if (res.data.State === 1) {
                        if (this.page === 1) {
                            this.list = data.DataRows;
                        } else {
                            this.list.push(...data.DataRows);
                        }
                        if (this.list.length >= data.TotalCount) {
                            this.finished = true;
                        }
                        this.loading = false;
                        this.isLoading = false;
                        this.refreshDom();
                    }
                    setTimeout(() => {
                        this.$toast.clear();
                    }, 200);
                })
                .catch(err => {
                    this.$toast.clear();
                    this.$toast("网络出现了问题");
                    this.finished = true;
                    this.loading = false;
                    this.isLoading = false;
                });
        },
        handleToDetails(id) {
            this.$router.push({ path: "/details" });
            this.handleChangeDetails(id);
        },
        handleToHome() {
            this.$router.back();
        },
        handleOnRefresh() {
            this.page = 1;
            this.getList();
        },
        handleOnLoad() {
            this.loading = true;
            this.getList();
            this.page++;
        },
        refreshDom() {
            let headerH = document.getElementsByClassName("van-nav-bar")[0]
                .offsetHeight;
            let viewH = window.screen.height;
            clearTimeout(this.time);
            this.time = setTimeout(() => {
                let listH = document.getElementById("integral-detail-shop")
                    .offsetHeight;
                if (listH < viewH - headerH) {
                    document.getElementById(
                        "integral-detail-shop"
                    ).style.height = viewH - headerH - 56 + "px";
                }
            }, 50);
        }
    }
};
</script>

